.form {
  width: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin: 10px;
}

.input {
  border: 1px solid black;
  border-radius: 5px;
  -webkit-flex-basis: 80%;
          flex-basis: 80%;
  background-color: transparent;
  outline: none;
  padding: 10px;
  padding-left: 15px;
}

.form-button {
  background-color: rgb(38, 0, 255);
  border: none;
  border-radius: 5px;
  outline: none;
  color: white;
  cursor: pointer;
  -webkit-flex-basis: 15%;
          flex-basis: 15%;
}

.form-button:hover {
  opacity: 0.9;
}

.form-button:active {
  -webkit-transform: translateY(2px);
          transform: translateY(2px);
}

.todoItem {
  padding: 10px 20px;
  font-size: 18px;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  border-bottom: 1px solid rgb(212, 202, 202);
  position: relative;
}

.todoItem label.active::after {
  content: '';
  height: 1px;
  background-color: black;
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
}

.todoItem input[type='checkbox'] {
  -webkit-appearance: none;
     -moz-appearance: none;
          appearance: none;
  width: 20px;
  height: 20px;
  border: 2px solid blue;
  border-radius: 4px;
  outline: none;
}

.todoItem span {
  -webkit-flex-basis: 30%;
          flex-basis: 30%;
  text-align: left;
}

.todoItem input[type='checkbox']:checked {
  background-color: blue;
}

.todoItem input[type='checkbox'] + span {
  height: 1px;
  position: absolute;
  left: 0;
  top: 50%;
}

.todoItem input[type='checkbox']:checked + span {
  background-color: black;
}

.todoItem input[type='checkbox']:focus,
.todoItem input[type='checkbox']:hover {
  box-shadow: 0 0 0 3px orangered;
  cursor: pointer;
}

.list {
  -webkit-flex-basis: 100%;
          flex-basis: 100%;
  list-style-type: none;
  padding: 0;
}

.App {
  text-align: center;
  padding: 20px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

